


<template>
  <div class="place content-box">
    <div class="content">
      <h4>我的旅程</h4>
      <hr />
      <b-row>
        <b-col cols="12">
          <b-card-group deck>
            <div class="cardcol" v-bind:key="trip.id" v-for="trip in trips">
                <b-card :title="trip.title" img-alt="Image" style="max-width: 39vh;" img-top img-height="200vh">
                <b-card-text>
                    {{ trip.title }}
                    {{ trip.id }}
                </b-card-text>
                <span @click="goUserTrip(trip)">開始旅程</span>
                </b-card>
              </div>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import { getUserAllTrip } from '@/api/trip'
import store from "@/store";

export default {
  name: "User",
  mounted() {
    this.getUserAllTrip()
  },
  data(){
    return{
        trips : [],
    }
  },
  methods: {
    getUserAllTrip() {
      return new Promise((resolve, reject) => {
        getUserAllTrip(store.getters.token)
          .then((response) => {
            console.log(JSON.stringify(response))
            this.trips = response
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    goUserTrip(data) {
      // alert(data.id)
      // alert(JSON.stringify(data))
      this.$router.push({
        name: "usertravel", //use name for router push
        params: { id:data.id, data: data }
      });
    }
  },
  store,
};
</script>

<style scoped>

.content{
  padding: 1vw;
}

h4 {
    padding-top: 2vh;
}

.card{
    padding-bottom: 2vh;
}

.cardcol{
    padding-top: 2vh;
}

</style>